.bbox {
  width: 437px;
  height: 110px;
}
.bbox ul {
  width: 100%;
  height: 86px;
  margin: 0;
  padding: 0;
}
.bbox ul li {
  margin-right: 15px;
  float: left;
  list-style: none;
  width: 70px;
  height: 77px;
  text-align: center;
}
.bbox ul li span {
  display: inline-block;
  width: 61px;
  height: 53px;
}
.bbox ul li p {
  color: #ffffff;
  font-size: 14px;
  margin-bottom: 0;
}
.bbox .sbox {
  width: 437px;
  height: 24px;
}
.bbox .sbox ul {
  width: 100%;
  height: 100%;
}
.bbox .sbox ul li {
  margin-right: 15px;
  width: 70px;
  height: 24px;
  background-color: #00a5ff;
  border-radius: 4px;
  opacity: 0.6;
  text-align: center;
  color: #ffffff;
  font-size: 14px;
}
.bbox .sou {
  width: 251px;
  height: 160px;
  position: relative;
  left: -700px;
  top: -100px;
}
.bbox .sou .sone {
  text-align: center;
  width: 251px;
  height: 45px;
  line-height: 45px;
  letter-spacing: 1px;
  font-size: 16px;
  color: #ffffff;
  padding-left: 24px;
}
.bbox .sou .sone span {
  font-size: 26px;
  letter-spacing: 1px;
  color: #ffffff;
}
.bbox .sou .stwo {
  width: 251px;
  height: 35px;
  background-color: #0479de50;
  border-radius: 6px;
  border: solid 1px #00ffff;
  margin-top: 66px;
  font-size: 20px;
  color: #ffffff;
}
.bbox .sou .stwo div {
  border-radius: 6px;
  width: 250px;
  height: 35px;
  background-color: #0479de50;
}
.bbox .sou .stwo div input {
  height: 35px;
  color: #ffffff;
}
.bbox .sou .sousuo {
  width: 22px;
  height: 22px;
  position: relative;
  top: -110px;
  left: 215px;
  z-index: 999;
}
